<template>
  <div>
    <!--  -->
    <!-- <pdf ref="pdf" :src="url"></pdf> -->
    <!--  -->
    <!-- <pdf ref="pdf" :src="url" v-for="i in numPage" :key="i" :page="i"></pdf> -->
    <!-- pdf  -->
    <el-button-group>
      <el-button
        type="primary"
        icon="el-icon-arrow-left"
        size="mini"
        @click="prePage"
        ></el-button
      >
      <el-button type="primary" size="mini" @click="nextPage"
        ><i class="el-icon-arrow-right el-icon--right"></i
      ></el-button>
    </el-button-group>
    <div style="marginTop: 10px; color: #409EFF">
      {{ pageNum }} / {{ pageTotalNum }}
    </div>
    <pdf
      :page="pageNum"
      :src="url"
      @progress="loadedRatio = $event"
      @num-pages="pageTotalNum = $event"
    ></pdf>
  </div>
</template>

<script>
import pdf from "vue-pdf";
export default {
  components: {
    pdf,
  },
  data() {
    return {
      url: "http://image.cache.timepack.cn/nodejs.pdf",
      numPage: null,
      pageNum: 1,
      pageTotalNum: 1, // 
      loadedRatio: 0, //0-1 1
    };
  },
  mounted() {
    this.getNumPages();
  },
  methods: {
    //pdf
    getNumPages() {
      let loadingTask = pdf.createLoadingTask(this.url);
      loadingTask.promise
        .then((pdf) => {
          this.numPage = pdf.numPage;
        })
        .catch((err) => {
          const toast = this.$createToast({
            time: 1000,
            txt: `pdf ${err}`,
          });
          toast.show();
        });
    },
    // 
    prePage() {
      let page = this.pageNum;
      page = page > 1 ? page - 1 : this.pageTotalNum;
      this.pageNum = page;
    },

    // 
    nextPage() {
      let page = this.pageNum;
      page = page < this.pageTotalNum ? page + 1 : 1;
      this.pageNum = page;
    },
  },
};
</script>

